export default function resetBorderButton() {
  const penToolBthn = document.getElementById('pen-tool');
  const eraserToolBthn = document.getElementById('eraser-tool');
  const paintRollerBthn = document.getElementById('paint-roller');
  const coloPickerBthn = document.getElementById('color-picker');
  const arrBthns = [];
  arrBthns.push(penToolBthn, eraserToolBthn, paintRollerBthn, coloPickerBthn);

  arrBthns.forEach((element) => {
    // eslint-disable-next-line no-param-reassign
    element.style.borderColor = '#cbcbcb';
  });
}
